export function getYMax(data) {
  return Math.max(...data.map((ele) => ele.duration));
}

export function generateYAxis(yMax, className) {
  return Array.from({ length: yMax + 1 }, (_, i) => {
    return (
      <div className={className} style={{ gridRow: `${yMax + 1 - i}`, gridColumn: 2 }}>
        {i}
      </div>
    );
  });
}

export function timerConverter(format = "day", data) {
  if (!format) return;
  if (format === "day") {
    return data.map((ele) => {
      const modifiedTime = new Date(ele.time).getDate();
      return { ...ele, time: modifiedTime };
    });
  }
  if (format === "time") {
    return data.map((ele) => {
      const modifiedTime = new Date(ele.time).getHours();
      return { ...ele, time: modifiedTime };
    });
  }
}

export function dataSorter(data, type) {
  if (type === "ascending") return data.sort((a, b) => a.time - b.time);
  if (type === "descending") return data.sort((a, b) => b.time - a.time);
}

export function generateXArray(data) {
  return data.map((ele) => ele.time);
}

export function removeDuplicates(array) {
  return [...new Set(array)];
}

export function generateXAxis(array, yMax, className) {
  return array.map((ele, i) => (
    <div className={className} style={{ gridRow: yMax + 1, gridColumn: i + 3 }}>
      {ele}
    </div>
  ));
}

export function generateXLabel(yMax, xMax, text) {
  return (
    <div style={{ gridArea: `${yMax + 2} / 2 / ${yMax + 3} / ${xMax + 3}` }}>
      <h6>{text}</h6>
    </div>
  );
}

export function generateYLabel(className, yMax, text) {
  return (
    <div className={className} style={{ gridArea: `1/1/${yMax + 3} / 2` }}>
      <h6>{text}</h6>
    </div>
  );
}

export function generateGraphData(data, yMax, array, markermrt, markerbus) {
  return data.map((ele, i) => {
    const xStart = array.findIndex((dd) => dd == ele.time) + 3;
    const yStart = yMax - ele.duration + 1;
    return (
      <div style={{ gridArea: `${yStart}/${xStart}/${yStart + 1}/${xStart + 1}` }}>
        {ele.route === "hillview" ? markermrt : markerbus}
      </div>
    );
  });
}
