import Form from "./Form/Form";
import HorbContext from "./store/HorbContext";
import { useContext, useState } from "react";
import LogList from "./Log/LogList";
import Button from "./UI/Button";
import Graph from "./Analytics/Graph";

const Main = () => {
  const [toggleForm, setToggleForm] = useState(false);
  const [toggleLog, setToggleLog] = useState(false);
  const horbCtx = useContext(HorbContext);

  function customFormHandler() {
    setToggleForm(true);
  }
  function customLogHandler() {
    setToggleLog(true);
  }

  return (
    <>
      {toggleForm && <Form submitHandler={setToggleForm} />}
      {!toggleForm && (
        <Button size="large" align="center" onClickHandler={customFormHandler}>
          New Entry
        </Button>
      )}
      {toggleLog && <LogList />}
      {!toggleLog && (
        <Button size="large" align="center" onClickHandler={customLogHandler}>
          Show Logs
        </Button>
      )}
      <Graph />
    </>
  );
};

export default Main;
