import styles from "./Graph.module.css";
import {
  generateXArray,
  generateXAxis,
  generateXLabel,
  generateYAxis,
  generateYLabel,
  generateGraphData,
  getYMax,
  removeDuplicates,
  timerConverter,
  dataSorter,
} from "./GraphHelper";
import GraphWrapper from "./GraphWrapper";

const DATASET = [
  { time: 1620400000000, route: "hillview", duration: 12 },
  { time: 1620500000020, route: "beauty world", duration: 10 },
  { time: 1620600000000, route: "hillview", duration: 9 },
  { time: 1620600000000, route: "beauty world", duration: 16 },
  { time: 1620700000000, route: "beauty world", duration: 8 },
  { time: 1620800000000, route: "hillview", duration: 11 },
  { time: 1620900000000, route: "hillview", duration: 10 },
  { time: 1620900000000, route: "beauty world", duration: 15 },
  { time: 1621000000000, route: "beauty world", duration: 12 },
  { time: 1621100000000, route: "hillview", duration: 15 },
  { time: 1621200000000, route: "beauty world", duration: 14 },
  { time: 1621300000000, route: "hillview", duration: 13 },
  { time: 1621400000000, route: "beauty world", duration: 15 },
  { time: 1621500000000, route: "beauty world", duration: 17 },
];

const Graph = (props) => {
  /**
   * Props Required:
   * data: DATASET
   * xAxis sort: ascending / descending
   * xAxsis parameter: day / time
   * xLabeltext
   * yLabeltext
   * markerHillview:
   * markerBeautyWorld:
   */
  const DATA_CONVERTED = timerConverter("day", DATASET);
  const DATA_SORTED = dataSorter(DATA_CONVERTED, "ascending");

  console.log(DATA_SORTED);
  const yMax = getYMax(DATASET);
  const yAxis = generateYAxis(yMax, styles.yAxis);

  const xArray = generateXArray(DATA_SORTED);
  const xArrayUnique = removeDuplicates(xArray);
  const xMax = xArrayUnique.length;

  const xAxis = generateXAxis(xArrayUnique, yMax, styles.xAxis);
  //   console.log(xArray, xArrayUnique);

  const xLabel = generateXLabel(yMax, xMax, "May (Day)");
  const yLabel = generateYLabel(
    styles.yLabel,
    yMax,
    "Minutes taken to reach home (from Beauty World)"
  );

  const graphData = generateGraphData(DATA_SORTED, yMax, xArrayUnique, "🚆", "🚍");

  return (
    <GraphWrapper size="large">
      <div
        style={{
          gridTemplateRows: `repeat(${yMax + 2}, 1fr)`,
          gridTemplateColumns: ` 1px repeat(${xArrayUnique.length + 1}, 1fr)`,
        }}
        className={styles.graph}
      >
        {graphData}
        {yAxis}
        {xAxis}
        {xLabel}
        {yLabel}
      </div>
    </GraphWrapper>
  );
};

export default Graph;
